"use client";

import { useFormContext } from "react-hook-form";
import type { ReactElement } from "react";

export default function AddressForm(): ReactElement {
    const {
        register,
        formState: { errors },
    } = useFormContext();

    return (
        <div>
            <h2 className="font-bold text-lg mb-4">Dirección de entrega</h2>

            {/* Dirección principal */}
            <div className="mb-3">
                <input
                    type="text"
                    placeholder="Ingresa tu dirección"
                    className={`w-full px-4 py-2 bg-[#2a2a2a] border rounded text-white placeholder-gray-400 focus:outline-none ${
                        errors.address ? "border-red-500" : "border-gray-600"
                    }`}
                    {...register("address")}
                />
                {errors.address && (
                    <p className="text-red-500 text-sm mt-1">
                        {errors.address.message as string}
                    </p>
                )}
            </div>

            {/* Instrucciones adicionales */}
            <input
                type="text"
                placeholder="Instrucciones de entrega (opcional)"
                className="w-full px-4 py-2 bg-[#2a2a2a] border border-gray-600 rounded text-white placeholder-gray-400 focus:outline-none"
                {...register("instructions")}
            />
        </div>
    );
}
