// app/checkout/page.tsx
"use client";

import { useState, type ReactElement, useMemo } from "react";
import Image from "next/image";
import { useForm, FormProvider, SubmitHandler } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";

import AddressForm from "./components/AddressForm";
import OrderItems from "./components/OrderItems";
import SummaryBox, { calcFeesUSD } from "./components/SummaryBox";
import { useCart } from "@/lib/cart-context";
import { createOrder, type OrderPayload } from "@/lib/services/orderService";
import { getTotalPromo } from "@/lib/precio-producto";

// — Esquema de validación —
const checkoutSchema = z.object({
  address: z.string().min(10, "La dirección es requerida (mín. 10 caracteres)."),
  instructions: z.string().optional(),
});
type CheckoutFormData = z.infer<typeof checkoutSchema>;

export default function PagoPage(): ReactElement {
  const [orderId, setOrderId] = useState<string | null>(null);
  const { items, clear: clearCart } = useCart();

  const methods = useForm<CheckoutFormData>({
    resolver: zodResolver(checkoutSchema),
  });
  const {
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  // Calculamos subtotal + fees
  const { total } = useMemo(() => {
    const sub = items.reduce(
      (s, it) =>
        s + getTotalPromo(it.promo, it.price, it.quantity).total,
      0
    );
    const { envio, servicio } = calcFeesUSD(sub);
    return { subtotal: sub, total: sub + envio + servicio };
  }, [items]);

  const onSubmit: SubmitHandler<CheckoutFormData> = async (data) => {
    // Aquí deberías obtener el usuario real desde tu contexto de auth.
    // Si no hay usuario, pasamos null.
    const currentUserId: number | null = /* lógica de auth */ null;

    const details = items.map((it) => ({
      id_product: Number(it.id),
      quantity: it.quantity,
      unit_price: getTotalPromo(it.promo, it.price, 1).total,
    }));

    const purchase = {
      id_user: currentUserId,    // ahora puede ser number o null
      status: "pending" as const,
      total,
    };

    const payload: OrderPayload = { purchase, details };

    try {
      const resp = await createOrder(payload);
      clearCart();
      setOrderId(String(resp.id));
    } catch (err: any) {
      console.error("Error al crear el pedido:", err);
      alert(err.message || "Hubo un error procesando tu pedido");
    }
  };

  return (
    <div className="relative isolate flex-1 flex items-center justify-center px-4 sm:px-8 py-8">
      <Image
        src="/images/superr.jpg"
        alt="Background supermercado"
        fill
        className="-z-20 object-cover"
        priority
      />
      <div className="absolute inset-0 -z-10 bg-gray-900/40 backdrop-blur-sm" />

      <div className="bg-[#1e1e1e]/90 backdrop-blur-sm rounded-2xl p-8 shadow-2xl w-full max-w-6xl text-white">
        {orderId ? (
          <div className="flex flex-col items-center text-center gap-6 py-16">
            <h2 className="text-3xl font-bold text-green-400">
              ¡Gracias por tu compra!
            </h2>
            <p className="text-lg">
              Tu número de pedido es:
              <br />
              <span className="font-mono text-yellow-300 text-2xl">
                {orderId}
              </span>
            </p>
            <p className="text-sm text-gray-300 max-w-md">
              Recibirás un correo con los detalles y actualizaciones del envío.
            </p>
          </div>
        ) : (
          <FormProvider {...methods}>
            <form onSubmit={handleSubmit(onSubmit)}>
              <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                <div className="space-y-6">
                  <AddressForm />
                  <OrderItems />
                </div>
                <div className="flex flex-col gap-4">
                  <p className="font-simpson text-outline-blue text-yellow-300 text-center text-lg">
                    ¡Gracias, vuelva pronto… y recuerde: por cada donut la quinta fuga de Squishee es cortesía de la casa!
                  </p>
                  <SummaryBox isProcessing={isSubmitting} />
                </div>
              </div>
            </form>
          </FormProvider>
        )}
      </div>
    </div>
  );
}
