// components/checkout/OrderItems.tsx
"use client";

// ── Imports ────────────────────────────────────────────────────────────────
import Image from "next/image";
import { useCart } from "@/lib/cart-context";
import { getTotalPromo } from "@/lib/precio-producto";

/**
 * **OrderItems**
 *
 * Lista los artículos del carrito en el flujo de checkout.
 * Calcula precios con cualquier promoción aplicada mediante `getTotalPromo`.
 *
 * _Consideraciones_
 * - Cuando el catálogo crezca, paginaremos o virtualizaremos la lista.
 */
export default function OrderItems() {
    const { items } = useCart();

    /* -------------------------- Carrito vacío --------------------------- */
    if (items.length === 0) {
        return (
            <p className="text-sm text-gray-400">
                No hay artículos en tu canasta.
            </p>
        );
    }

    /* --------------------------- Render lista --------------------------- */
    return (
        <div className="space-y-4">
            <h3 className="font-semibold text-lg">Tu pedido</h3>

            {items.map((it) => {
                // Precios con promo (total, unitario, precio antes del descuento)
                const { total, old } = getTotalPromo(
                    it.promo,
                    it.price,
                    it.quantity
                );

                return (
                    <div key={it.id} className="flex items-center gap-4">
                        {/* Imagen del producto */}
                        <Image
                            src={it.image ?? "/placeholder.png"}
                            alt={it.name}
                            width={48}
                            height={48}
                            className="w-12 h-12 rounded-full object-cover"
                        />

                        {/* Nombre + unidades */}
                        <div className="flex-1">
                            <p className="text-sm font-medium">{it.name}</p>
                            <p className="text-xs text-gray-400">
                                {it.quantity} Unidades
                            </p>
                        </div>

                        {/* Sub-total */}
                        <span className="text-sm">
                            {old ? (
                                <>
                                    <span className="line-through mr-1 text-red-400">
                                        ${old.toFixed(2)}
                                    </span>
                                    <span className="text-green-400">
                                        ${total.toFixed(2)}
                                    </span>
                                </>
                            ) : (
                                `$${total.toFixed(2)}`
                            )}
                        </span>
                    </div>
                );
            })}

            {/* ETA */}
            <p className="text-xs text-gray-400">
                Entrega estimada:&nbsp;
                <span className="font-semibold text-white">
                    49 – 69&nbsp;min
                </span>
            </p>
        </div>
    );
}
