// components/checkout/CostRow.tsx
"use client";

/**
 * Props de `CostRow`.
 * @property label Texto descriptivo (p. ej. "Subtotal", "Envío").
 * @property value Importe numérico (se mostrará con dos decimales).
 */
interface Props {
  label: string;
  value: number;
}

/**
 * **CostRow**
 *
 * Fila con descripción y valor monetario formateado.
 * Se usa dentro del resumen de pago.
 */
export default function CostRow({ label, value }: Props) {
  return (
    <div className="flex justify-between text-gray-300 text-sm">
      <span>{label}</span>
      <span>${value.toFixed(2)}</span>
    </div>
  );
}
